header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* ================= CTA ==============*/
.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* ================= Header Socials ==============*/
.socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.socials::after {
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* ================= Me ==============*/
.me {
    background: linear-gradient(var
    (--color-primary), transparent);
    width: 22rem;
    height: 40rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 2rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
}

.me img{
    width: 100%; /* or any custom size */
}
/* ================= Scroll Down ==============*/
.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ============== MEDIA QUERIES (MEDIUM DEVICES) ============== */
@media screen and (max-width: 1024px) {
    header {
        height: 90vh;
    }
}

/* ============== MEDIA QUERIES (Small DEVICES) ============== */
@media screen and (max-width: 600px) {
    header {
        height: 120vh;
    }

    .socials,
    .scroll__down {
        display: none;
    }
}