.academics__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.degrees {
    border: 1px solid transparent;
    display: grid;
    grid-template-rows: 10rem 3rem 3rem;
    gap: 1rem;
    
}

.major {
    border: 1px solid transparent;
    background: var(--color-primary);
    border-radius: 2rem;
    text-align: center;
    color: var(--color-bg);
    transition: var(--transition);
}

.major h2{
    color: var(--color-bg-variant);
    font-weight: 200;
}

.major__content {
    margin-top: 1.5rem;
}

.minor {
    border: 1px solid transparent;
    background: var(--color-bg-variant);
    border-radius: 2rem;
    text-align: center;
    color: var(--color-primary);
    transition: var(--transition);
}

.minor__content {
    margin-top: .3rem;
}

.minor:hover {
    background: transparent;
    border-color: var(--color-bg-variant);
    cursor: default;
    color: var(--color-bg-variant);
}

.major:hover {
    background: transparent;
    border-color: var(--color-bg-variant);
    cursor: default;
    color: var(--color-primary);
}

.courses {
    border: 1px solid transparent;
    background: var(--color-bg-variant);
    border-radius: 2rem;
    color: var(--color-primary);
    transition: var(--transition);
}

.courses:hover {
    background: transparent;
    border-color: var(--color-bg-variant);
    cursor: default;
    color: var(--color-bg-variant);
}

.courses__content h3 {
    text-align: center;
}

.courses__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin-left: 3rem;
    margin-top: 1rem;
}

.courses__icon {
    color: var(--color-primary);
    margin-top: 1rem;
}

/* ============== MEDIA QUERIES (MEDIUM DEVICES) ============== */
@media screen and (max-width: 1024px) {
    .academics__container {
        grid-template-columns: 1fr;
        gap: 2rem;
    }
    .courses__list{
        grid-template-columns: 1fr 1fr;
    }
}

/* ============== MEDIA QUERIES (Small DEVICES) ============== */
@media screen and (max-width: 600px) {
    .academics__container {
        grid-template-columns: 1fr;
        gap: 2rem;
    }
    .courses__list{
        grid-template-columns: 1fr;
    }
}